import '../../../../node_modules/particles.js/particles.js';

/**
 * This pattern loads the particle.js NMP module. The function below executes the animation with given parameters below.
 * For full documentation please refer to https://github.com/VincentGarreau/particles.js
 * The function applies the particle animation to a div with the ID 'particles-js'.
 */

const className = 'particle-animation';

function initParticles() {
    particlesJS('particles-js', {
        particles: {
            number: {
                value: 80,
            },
            color: {
                value: '#e5e5e5',
            },
            shape: {
                type: 'circle',
                stroke: {
                    width: 0,
                    color: '#000000',
                },
                polygon: {
                    nb_sides: 5,
                },
                image: {
                    src: 'img/github.svg',
                    width: 100,
                    height: 100,
                },
            },
            opacity: {
                value: 0.8,
                random: false,
                anim: {
                    enable: false,
                    speed: 1,
                    opacity_min: 0.4,
                    sync: false,
                },
            },
            size: {
                value: 7,
                random: true,
                anim: {
                    enable: false,
                    speed: 40,
                    size_min: 3,
                    sync: false,
                },
            },
            line_linked: {
                enable: true,
                distance: 150,
                color: '#e5e5e5',
                opacity: 0.9,
                width: 2,
            },
            move: {
                enable: true,
                speed: 1,
                direction: 'none',
                random: false,
                straight: false,
                out_mode: 'out',
                bounce: false,
                attract: {
                    enable: false,
                    rotateX: 600,
                    rotateY: 1200,
                },
            },
        },
        interactivity: {
            detect_on: 'canvas',
            events: {
                onhover: {
                    enable: true,
                    mode: 'grab',
                },
                onclick: {
                    enable: true,
                    mode: 'push',
                },
                resize: true,
            },
            modes: {
                grab: {
                    distance: 140,
                    line_linked: {
                        opacity: 1,
                    },
                },
                bubble: {
                    distance: 400,
                    size: 40,
                    duration: 2,
                    opacity: 8,
                    speed: 3,
                },
                repulse: {
                    distance: 200,
                    duration: 0.4,
                },
                push: {
                    particles_nb: 4,
                },
                remove: {
                    particles_nb: 2,
                },
            },
        },
        retina_detect: true,
    });
}

export default {
    launchFn: initParticles,
    launchQuery: `.${className}`,
};