import patterns from './patterns';
import { removeClass, parametersToObject, gaEvent, detectIE } from './util';
import { devcorate } from './patterns/devcorate/devcorate';
import prepareAccordionTabs from './patterns/tabs/prepareAccordionTabs';

const edgeVersion = 12;

/**
 * try a function, log the error if it throws an exception.
 *
 * @param {Function} f - The function to call.
 */
export function tryCatch(f) {
    try {
        return f();
    } catch (e) {
        // console.log(e);
        gaEvent(
            'jsError',
            'JavaScript error',
            `Line ${e.lineNumber}  ${e.message}`,
            `Pattern launch ${e.fileName} (${window.location})`,
            true
        );
    }
}

/**
 * Runs the function for each pattern against every element that has the
 * matching class, unless the element also has that class with a '-no-js' suffix
 * (for cases where you want to use the CSS for a pattern, but not the
 * functionality).
 *
 * @param {object} pattern - An object containing the class name to trigger the pattern and the launch function to run for those elements.
 */
function launchPattern(pattern) {
    if (typeof pattern === 'function') {
        tryCatch(pattern);
    } else if (pattern.launchQuery) {
        const { launchFn, launchQuery } = pattern;
        Array.from(document.querySelectorAll(launchQuery))
            .filter(elem => elem.className.indexOf(`${launchQuery}-no-js`))
            .forEach(elem => tryCatch(() => launchFn(elem)));
    }
}

/**
 * Recursively generate accordion--tabs HTML for tabs.
 *
 * @param {HTMLElement} root
 */
function accordionizeTabs(root) {
    Array.from(root.querySelectorAll('.tabs'))
        .filter(elem => elem.className.indexOf('.tabs-no-js'))
        .forEach(elem => {
            const potential = tryCatch(() => prepareAccordionTabs(elem));
            potential && accordionizeTabs(potential);
        });
}

document.addEventListener(
    'DOMContentLoaded',
    () => {
        if (/(Trident|MSIE)/.test(navigator.userAgent)) {
            return;
        } else {
            // disable for now
            // if ('serviceWorker' in navigator) {
            //     window.addEventListener('load', function() {
            //         navigator.serviceWorker
            //             .register('sw.js')
            //             .then(function() {}, function() {});
            //     }, false);
            // }
            Array.from(document.getElementsByTagName('html')).forEach(html => {
                let ie = detectIE();
                removeClass(html, 'no-js', false);
                if (ie >= edgeVersion) {
                    html.className = (html.className + ' js ' + 'edge').trim();
                } else {
                    html.className = (html.className + ' js').trim();
                }
            });

            accordionizeTabs(document);
            patterns.forEach(launchPattern);

            const parameters = parametersToObject(location.search);
            parameters['dev'] &&
                devcorate(
                    document.querySelector('body'),
                    'dev',
                    parameters['dev']
                );
            parameters['rel'] &&
                devcorate(
                    document.querySelector('body'),
                    'rel',
                    parameters['rel']
                );
        }
    },
    false
);
